import uniIcons from '../uni-icons/uni-icons.vue'
export default{
	data(){
		return{
			animationData:{},
			 off: true,
			 clickIcon:"more-filled",
			navIcon:[
				{
					url:"../index/index",
					ico:"home",
					text:"首页"
				},
				{
					url:"../../pages/classify/classify",
					ico:"shop",
					text:"微商城"
				},
				{
					url:"../../pages/index/index",
					ico:"paperplane",
					text:"装修攻略"
				},
				{
					url:"../../pages/cart/test2",
					ico:"cart",
					text:"购物车"
				},
				{
					url:"../../pages/user/user",
					ico:"person",
					text:"个人中心"
				},
			],
		}
	},
	components:{
		uniIcons
	},
	created(){
		// 初始化一个动画
		var animation = uni.createAnimation({
			duration: 500,
			timingFunction: 'ease',
		})
		this.animation = animation
	},
	 
	methods:{
		
		backtrack(){
			uni.navigateBack()
		},
		
		
		declick() {
			if (this.off) {
				// 使用动画
				this.rotateAndScale()
			} else {
				this.norotateAndScale()
			}
			this.off = !this.off
		},
		// 定义动画内容
		rotateAndScale() {
			// 定义动画内容
			this.animation.width(400).opacity(1).step()
			this.clickIcon = 'arrowleft'
			// this.animation.rotate(45).scale(2, 2).step()
			// 导出动画数据传递给data层
			this.animationData = this.animation.export()
		},
		norotateAndScale() {
			// this.animation.rotate(0).scale(1, 1).step()
			this.animation.width(0).opacity(0).step()
			this.clickIcon = 'more-filled'
			this.animationData = this.animation.export()
		}
		 
	}
}
